body {
  margin: 0;
  font-family: "PT Mono", "Courier", monospace;
  font-style: normal;
  font-size: 16px; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.button {
  height: 45px;
  background-color: transparent;
  border-radius: 5px;
  border-width: 1px;
  border-style: solid;
  color: #ffffff;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-align: center; }

